/* Fonts */
@font-face {
  font-family: "Kaushan Script";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/KaushanScript-Regular.woff2") format("woff2"), url("../fonts/KaushanScript-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-display: swap;
}
/* Normalize */
body {
  margin: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* COntainer */
.container {
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 15px;
}

/* Intro */
.intro {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  background-image: url(../img/intro/intro-bg.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #e89584;
}

.intro__main {
  margin-top: 90px;
}

/* Header */
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Logo */
.logo__link {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 30px;
  text-decoration: none;
  color: #fff;
  padding: 34px 0;
}
.logo__link:hover {
  color: #fce38a;
  transition: color 0.3s linear;
}

/* Main-nav */
.main-nav__list {
  display: flex;
  align-items: center;
  width: 620px;
  max-width: 620px;
  justify-content: space-between;
  list-style: none;
  margin: 0;
  padding: 0;
}

.main-nav__link {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  line-height: 1.6;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  padding: 40px 0;
  position: relative;
}
.main-nav__link--current {
  color: #fce38a;
  cursor: pointer;
}
.main-nav__link--current::after {
  content: "";
  display: block;
  width: 100%;
  height: 3px;
  background-color: #fce38a;
  position: absolute;
  bottom: 33px;
  left: 0;
}
.main-nav__link:not(.main-nav__link--current):hover {
  color: #fce38a;
  transition: color 0.3s linear;
}
.main-nav__link:not(.main-nav__link--current)::after {
  content: "";
  display: block;
  width: 100%;
  height: 3px;
  background-color: #fce38a;
  position: absolute;
  bottom: 33px;
  left: 0;
  transform: scale(0);
}
.main-nav__link:not(.main-nav__link--current):hover::after {
  transform: scale(1);
  transition: transform 0.3s linear;
}
.main-nav__link:hover svg path {
  fill: #fce38a;
  transition: fill 0.3s linear;
}

/* Main */
.main {
  text-align: center;
}

.main__suptitle {
  font-family: "Kaushan Script", sans-serif;
  font-size: 72px;
  color: #fff;
  margin-bottom: 20px;
}

.main__title {
  max-width: 850px;
  margin: 0 auto;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 150px;
  text-transform: uppercase;
  line-height: 0.9;
  color: #fff;
}
.main__title::after {
  content: "";
  display: block;
  width: 60px;
  height: 3px;
  background-color: #fff;
  margin: 65px auto 35px;
}

/* Button */
.button {
  display: inline-block;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 1;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  border: 3px solid #fff;
  padding: 12px 30px;
}
.button:hover {
  background-color: #000;
  transition: background-color 0.3s linear;
}

/* Footer */
.footer {
  margin-top: auto;
}

.footer__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  width: 100%;
}

.footer__item {
  flex-basis: 25%;
  flex-grow: 1;
}

.footer__link {
  display: block;
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  padding: 30px 0;
  position: relative;
}
.footer__link::before {
  content: "";
  display: block;
  width: 280px;
  height: 3px;
  background-color: #fff;
  position: absolute;
  top: 10px;
  left: 0;
}
.footer__link:not(.footer__link--current):hover {
  transition: color 0.3s linear;
  color: #4a4a4a;
}
.footer__link:not(.footer__link--current)::after {
  content: "";
  display: block;
  width: 70px;
  height: 3px;
  z-index: 2;
  background-color: #4a4a4a;
  position: absolute;
  top: 10px;
  left: 0;
  opacity: 0;
  transition: opacity 0.2s linear;
}
.footer__link:not(.footer__link--current):hover::after {
  opacity: 1;
}

.footer__link--current {
  color: #f38181;
}
.footer__link--current:after {
  content: "";
  display: block;
  width: 70px;
  height: 3px;
  z-index: 2;
  background-color: #f38181;
  position: absolute;
  top: 10px;
  left: 0;
}

.footer__item b {
  font-weight: 700;
  font-size: 24px;
}